import React, { useEffect, useState, useRef } from 'react';
import { Layout, Modal, Input } from 'antd';

import SideBar from './sidebar';
import Chat from './pages/chat';

import { getEventMsg, parseJson } from './utils/ws';

import './App.css';
import 'antd/dist/antd.css';

const { Content, Sider } = Layout;

function App() {
  const inputRef = useRef(null);
  const [user, setUser] = useState(null);
  const [currentChatUser, setCurrentChatUser] = useState(null);
  const [chatStore, setChatStore] = useState({});
  const [userList, setUserList] = useState([]);
  const [socket, setSocket] = useState(null);
  const [collapsed, setCollapsed] = useState(false);
  const [newMessage, setNewMessage] = useState(null);

  useEffect(() => {
    if (!user || socket) return;
    const webSocket = new WebSocket("wss://morning-gorge-90448.herokuapp.com/ws");
    // const webSocket = new WebSocket("ws://192.168.30.83:8080/ws");
    console.log("Attempting Connection...");

    webSocket.onopen = () => {
      console.log("Successfully Connected");
      webSocket.send(getEventMsg("userJoined", user))
    };

    webSocket.onclose = event => {
      console.log("Socket Closed Connection: ", event);
      webSocket.send("Client Closed!")
    };

    webSocket.onerror = error => {
      console.log("Socket Error: ", error);
    };

    webSocket.onmessage = (data) => {
      const event = parseJson(data.data);
      if (!event) return;
      switch (event.Type) {
        case "onUserChanged":
          setUserList((Object.keys(event.Data).map(key => ({ name: key })).filter(o => o.name != user)));
          break;
        case "onMessageReceived":
          setNewMessage(event.Data);
          break;
      }
    }

    setSocket(webSocket);
    return () => {
      if (socket) socket.close();
    };
  }, [user]);

  useEffect(() => {
    if (!newMessage) return;
    const newChatStore = JSON.parse(JSON.stringify(chatStore));
    let chatId = null;
    if (newMessage.from === user) {
      chatId = newMessage.to;
    } else {
      chatId = newMessage.from;
    }
    if (!newChatStore[chatId]) newChatStore[chatId] = [];
    newChatStore[chatId].push(newMessage);
    setChatStore(newChatStore);
  }, [newMessage]);

  const onEnterName = () => {
    if (inputRef.current.input.value) {
      setUser(inputRef.current.input.value);
    }
  }

  const onMessageSend = (message) => {
    if (!message) return;
    socket.send(getEventMsg("onMessageSend", {
      To: currentChatUser,
      Chat: currentChatUser,
      From: user,
      Message: message
    }))
  }

  return (
    <Layout style={{ minHeight: '100vh' }}>
      {
        user
          ? <React.Fragment>
            <Sider collapsed={collapsed} onCollapse={value => setCollapsed(value)}>
              <SideBar currentChatUser={currentChatUser} setCurrentChatUser={setCurrentChatUser} userList={userList} />
            </Sider>
            <Layout className="site-layout">
              <Content className='content-main'>
                <Chat currentChatUser={currentChatUser} user={user} messageList={chatStore[currentChatUser]} onMessageSend={onMessageSend} />
              </Content>
            </Layout>
          </React.Fragment>
          : <Modal title="Enter name" visible={!user} footer={null}>
            <Input autoFocus onPressEnter={onEnterName} ref={inputRef} placeholder="Name" />
          </Modal>
      }
    </Layout>
  );
}

export default App;
