import React from 'react';
import { Avatar } from 'antd';
import "./index.css";

const SideBar = ({ userList, setCurrentChatUser, currentChatUser }) => {
    return (
        <div>
            {userList.map(user => (<div onClick={() => setCurrentChatUser(user.name)} className={`chat-list-user${currentChatUser === user.name ? " current-user" : ""}`}>
                <div className='user-avatar'>
                    <Avatar size={40} icon={user.name[0]} />
                </div>
                {user.name}
            </div>))}
        </div>
    );
};

export default SideBar;
