import React, { useRef, useState, } from 'react';
import { Input } from 'antd';
import { ArrowRightOutlined } from '@ant-design/icons';

import './index.scss';

const Chat = ({ onMessageSend, messageList = [], user, currentChatUser }) => {
    const [message, setMessage] = useState("")
    // const msgInputRef = useRef(null);

    const onSend = () => {
        console.log("🚀 ~ file: index.js ~ line 17 ~ onSend ~ onSend")
        // const message = msgInputRef.current.input.value;
        if (!message) return;
        onMessageSend(message);
        setMessage("");
    }

    return (
        <div className='chat-wrapper'>
            {
                !currentChatUser
                    ? <div className='select-chat'>
                        Select Chat
                    </div>
                    : <React.Fragment>
                        <div className='message-list-wrapper'>
                            {
                                messageList.map((message) => (
                                    message.from === user
                                        ? <div className='message-wrapper-sending'>
                                            <span className='message'>
                                                {message.message}
                                            </span>
                                        </div>
                                        : <div className='message-wrapper-incoming'>
                                            <span className='message'>
                                                {message.message}
                                            </span>
                                        </div>
                                ))
                            }
                        </div>
                        <div className='input-wrapper'>
                            <Input
                                value={message}
                                onChange={e => setMessage(e.target.value)}
                                onPressEnter={onSend}
                                className='message-input'
                                placeholder="Message"
                                suffix={<ArrowRightOutlined onClick={onSend} className="send-message-icon" />}
                            />
                        </div>
                    </React.Fragment>
            }
        </div>
    );
};

export default Chat;
